import React from "react";
import avatar from "../../images/avatar.png";
import add from "../../images/add.png";
import Multiselect from "multiselect-react-dropdown";

const AdminAddProducts = () => {
  const onSelect = () => {};
  const onRemove = () => {};
  const options = [
    { name: " ", id: 1 },
    { name: " ", id: 2 },
  ];
  return (
    <div className="grid grid-cols-12">
      <div className="font-extrabold text-[22px] text-[#555550] mb-5 col-span-12">
          
      </div>
      <div className="col-span-8 max-sm:col-span-12">
        <p className="gray mb-2"> </p>
        <img src={avatar} className="w-32 mb-4" />
        <input
          type="text"
          placeholder=" "
          className="p-2 rounded-lg bg-gray-100 border border-gray-300 w-full mb-4"
        />
        <textarea
          className="bg-gray-100 border border-gray-300 p-2 w-full h-28 rounded-lg mb-4"
          placeholder=" "
        ></textarea>
        <div className="flex flex-col">
          <input
            type="number"
            placeholder="  "
            className="mb-4 p-2 bg-gray-100 border border-gray-300 rounded-lg"
          />
          <input
            type="number"
            placeholder=" "
            className="mb-4 p-2 bg-gray-100 border border-gray-300 rounded-lg"
          />
        </div>
        <select
          className="gray w-full p-2 rounded-lg bg-gray-100 border border-gray-300 mb-4"
          placeholder=" "
        >
          <option> </option>
          <option> </option>
          <option> </option>
          <option> </option>
          <option> </option>
        </select>
          <Multiselect
            className="mb-4"
            placeholder=" "
            options={options}
            onSelect={onSelect}
            onRemove={onRemove}
            displayValue="name"
          />
        <select
          className="gray w-full p-2 rounded-lg bg-gray-100 border border-gray-300 mb-4"
          placeholder=""
        >
          <option></option>
          <option>  </option>
          <option>  </option>
          <option>  </option>
        </select>
        <div className="gray mb-1">  </div>
        <div className="mt-1 flex items-center gap-2">
          <div className="bg-red-500 w-8 h-8 rounded-full cursor-pointer border border-slate-200"></div>
          <div className="bg-white w-8 h-8 rounded-full cursor-pointer border border-slate-200"></div>
          <div className="bg-black w-8 h-8 rounded-full cursor-pointer border border-slate-200"></div>
          <img src={add} className="w-8" />
        </div>
        <div className="flex justify-end w-full mb-4">
          <button className="duration-500 text-white hover:text-[#555550] bg-black py-3 px-6 rounded-lg">
             
          </button>
        </div>
      </div>
    </div>
  );
};

export default AdminAddProducts;
